<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process281"></a>

<p><br></p>
<p><span class="rvts17">结局1：平分神权</span></p>
<p><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1532.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">该结局的必要条件是队伍中不能有洛许，因为洛许的任务是杀掉医生，所有的桥段都会指向这个剧情。另外，如果是费恩作为主角，那么另一个必要条件就是费恩不能宣誓神王。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1533.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">该结局的前提条件请参考</span><span class="rvts20"></span><span class="rvts21">医生的嘱托</span><span class="rvts20"></span><span class="rvts21">支线，在邪恶医生住所的海岸处，会发现一个奇怪的木桶（有箭头标示），如果你在浮木城的码头了解到码头的那些有毒的鱼是运往阿克斯的，并且在血月岛打败巴萨坦（</span><span class="rvts20">Basatan</span><span class="rvts21">）后从他身上得到了一张</span><span class="rvts20"></span><span class="rvts21">空虚鱼交易卷轴（</span><span class="rvts20">Voidwoken Fish Scroll</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">，那么可以把背包中的</span><span class="rvts20"></span><span class="rvts21">传送金字塔</span><span class="rvts20"></span><span class="rvts21">放入该木桶，并使用卷轴，把木桶传送到医生地下室深处。巴萨坦是血月岛提倡者的一个跟班儿，很容易找到。另外，木桶中放入在死神海岸收集的各种</span><span class="rvts20"></span><span class="rvts21">有毒的虚空鱼</span><span class="rvts20"></span><span class="rvts21">，使用传送卷轴后，会换回很多金币。别忘了，想要和医生签条约平分神权，一定要把</span><span class="rvts20">1</span><span class="rvts21">枚传送金字塔放进去，只有这样才可以把队伍传送到医生地下的秘密监狱，也才会给医生一个大大的惊喜。（详细参考</span><span class="rvts20"></span><span class="rvts21">医生的嘱托</span><span class="rvts20"></span><span class="rvts21">支线）</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1534.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">之后，利用金字塔把所有角色传送到那里。通过各种开锁，最终来到医生房间。这会使得医生非常惊讶！然后与他的对话，就会多出一个可以与你做交易的对话选项。交易内容是，他会在最后的战斗中帮你除掉达利斯和卢西安，但是你得在最后杀掉所有队友，然后和他平分神权。然而，如果队伍中的主角是费恩的话，也会遇到这种情况。费恩</span><span class="rvts20"></span><span class="rvts21">誓约</span><span class="rvts20"></span><span class="rvts21">神王后，会被医生拒绝门外，因为恶魔医生和神王明显是两个对立的势力。经测试，即便费恩使用</span><span class="rvts20"></span><span class="rvts21">破誓者</span><span class="rvts20"></span><span class="rvts21">解除和神王的契约，依然会被医生判定为</span><span class="rvts20"></span><span class="rvts21">敌人</span><span class="rvts20"></span><span class="rvts21">。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1535.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">另外，如果在</span><span class="rvts20"></span><span class="rvts21">矮人的秘密</span><span class="rvts20"></span><span class="rvts21">支线中，释放了埋藏在阿克斯地下的</span><span class="rvts20"></span><span class="rvts21">死亡迷雾</span><span class="rvts20"></span><span class="rvts21">，那么全城人将会死光。但是，医生这里只是停电停水，她的护士和她活的好好的，不过她会拒绝与你交往。你只能通过虚空鱼桶从她的秘密地下室到她的办公室找到她。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



